import { LIFE_CYCLE_PARAMS } from "../kit/events/events";
import PlatformSystem from "../kit/framework/platform/PlatformSystem";
import AudioManager from "../kit/system/audio/AudioManager";
import EventSystem from "../kit/system/event/EventSystem";
import TimerSystem from "../kit/system/timer/TimerSystem";
import { loadBundles } from "../kit/utils/bundleUtils";

const { ccclass, property } = cc._decorator;

const cdn_root = 'https://math-thinking.oss-cn-beijing.aliyuncs.com/qt/tools/book/ice-v1.2'
const book_id = 'v5'
const CONFIG = { id: 1, main: 'book_player', assets: [
    // 'https://shi-xintao.github.io/bundleTest/EduRemark',
    `${cdn_root}/internal-ice`,
    `${cdn_root}/resources-ice`,
    `${cdn_root}/main-ice`,
    `${cdn_root}/${book_id}`,
    'common', 
    'book_player', 
], params: {bundle:`${cdn_root}/${book_id}`,} }
// const CONFIG = { id: 1, main: 'redSantaClaus', assets: ['common','templateGame','redSantaClaus'], params: { url: "" } }
@ccclass
export default class main extends cc.Component {

    @property(cc.Node)
    contentNode: cc.Node = null;

    private lessonNode: cc.Node = null;

    onLoad() {
        this.relayout()
        cc.debug.setDisplayStats(false);
        PlatformSystem.instance.init();
        TimerSystem.instance.init();
        AudioManager.init();

        this.loadLocalProject();
    }

    private lock = false
 
    public relayout() {
        if (this.lock) {
            return
        }
        this.lock = true
        // 
        let frameSize = cc.view.getFrameSize() // 
        let designResolution = cc.Canvas.instance.designResolution
        let designSize = cc.size(designResolution.width, designResolution.height)
        cc.log("canvas designSize:", designSize)
        let frameAspectRatio = frameSize.width / frameSize.height
        let designAspectRatio = designSize.width / designSize.height
        let screenSize = { ...designSize } as cc.Size
 
        if (frameAspectRatio < designAspectRatio) {
            screenSize.height = Math.ceil(designSize.width / frameAspectRatio)
        } else {
            screenSize.width = Math.ceil(designSize.height * frameAspectRatio)
        }
        // (canvas)
        cc.Canvas.instance.designResolution = screenSize
 
        this.lock = false
    }

    // bundle
    private loadLocalProject(): void {
        let list: string[] = []
        CONFIG.assets.forEach((url: string) => {
            list.push(`${url}`)
        });
        console.log("list: ", list);

        let bundle: cc.AssetManager.Bundle = null
        loadBundles(list).then((res: cc.AssetManager.Bundle[]) => {
            bundle = res.find((element: cc.AssetManager.Bundle) => {
                return element.name === CONFIG.main
            })
            // 
            bundle.load(`${CONFIG.main}`, cc.Prefab, (error: Error, prefab: cc.Prefab) => {
                if (error) {
                    cc.error(error);
                    return;
                }
                this.lessonNode = cc.instantiate(prefab);
                let comp = this.lessonNode.getComponent("BaseChapterComponent");
                comp && comp.initParams(CONFIG.params);
                this.contentNode.addChild(this.lessonNode);

                //
                TimerSystem.instance.doFrameOnce(1, () => {
                    EventSystem.emit(LIFE_CYCLE_PARAMS, CONFIG.params);
                });
            })

        }).catch((error) => {
            cc.error(error)
        })
    }
}
