export class HttpStateMap {
  private static _instance: HttpStateMap;

  public static readonly map =
    "100\tContinue\t\n" +
    "101\tSwitching Protocols\tHTTP\n" +
    "0\terror\thttps://codeday.me/bug/20170705/34070.html\n" +
    "200\tOK\tGETPOST\n" +
    "201\tCreated\t\n" +
    "202\tAccepted\t\n" +
    "203\tNon-Authoritative Information\tmeta\n" +
    "204\tNo Content\t\n" +
    "205\tReset Content\t\n" +
    "206\tPartial Content\tGET\n" +
    "300\tMultiple Choices\t\n" +
    "301\tMoved Permanently\tURIURIURIURI\n" +
    "302\tFound\t301URI\n" +
    "303\tSee Other\t301GETPOST\n" +
    "304\tNot Modified\t\n" +
    "305\tUse Proxy\t\n" +
    "306\tUnused\tHTTP\n" +
    "307\tTemporary Redirect\t302GET\n" +
    "400\tBad Request\t\n" +
    "401\tUnauthorized\t\n" +
    "402\tPayment Required\t\n" +
    "403\tForbidden\t\n" +
    '404\tNot Found\t""\n' +
    "405\tMethod Not Allowed\t\n" +
    "406\tNot Acceptable\t\n" +
    "407\tProxy Authentication Required\t401\n" +
    "408\tRequest Time-out\t\n" +
    "409\tConflict\tPUT\n" +
    "410\tGone\t410404410301\n" +
    "411\tLength Required\tContent-Length\n" +
    "412\tPrecondition Failed\t\n" +
    "413\tRequest Entity Too Large\tRetry-After\n" +
    "414\tRequest-URI Too Large\tURIURI\n" +
    "415\tUnsupported Media Type\t\n" +
    "416\tRequested range not satisfiable\t\n" +
    "417\tExpectation Failed\tExpect\n" +
    "500\tInternal Server Error\t\n" +
    "501\tNot Implemented\t\n" +
    "502\tBad Gateway\t\n" +
    "503\tService Unavailable\tRetry-After\n" +
    "504\tGateway Time-out\t\n" +
    "505\tHTTP Version not supported\tHTTP";

  private data: any;

  public static get instance(): HttpStateMap {
    if (HttpStateMap._instance == null) {
      HttpStateMap._instance = new HttpStateMap();
    }
    return HttpStateMap._instance;
  }

  constructor() {
    var line: Array<string> = HttpStateMap.map.split("\n");
    this.data = {};
    line.forEach(element => {
      // var vo:HttpInfoVo=new HttpInfoVo();
      var temp: Array<string> = element.split("\t");
      this.data[temp[0]] = temp[1] + "\t" + temp[2];
    });
  }

  /**
   * http
   * @param id
   */
  public getErrorInfoById(id: number): void {
    return this.data[id];
  }
}

