import LogSystem from "../system/log/LogSystem";

/**
 * 
 * @version 1.0
 */
export class MonitorUtils {
  private static showData: any = {};
  private static elem: HTMLElement = null;

  public static init(): void {
    this.fps();
    this.elem = document.createElement("monitor");
    this.elem.setAttribute("width", "200");
    this.elem.setAttribute("height", "400");
    this.elem.style.fontSize = "10px";
    document.body.appendChild(this.elem);
    setInterval(() => {
      this.performance();
      this.elem.innerHTML = this.getInfo();
    }, 1000);
  }

  /** FPS */
  private static fps(): void {
    const times = []; // 
    let fps: number = 0;
    function refreshLoop() {
      window.requestAnimationFrame(() => {
        const now = (performance || Date).now();
        while (times.length > 0 && times[0] <= now - 1000) {
          times.shift(); // 1
        }
        times.push(now);
        fps = times.length;
        refreshLoop();
        MonitorUtils.showData["FPS"] = fps;
      });
    }
    refreshLoop();
  }

  /**
   * performance 
   */
  private static performance(): void {
    let performance =
      window["performance"] || window["msPerformance"] || window["webkitPerformance"];

    if (!performance) {
      // 
      // LogSystem.ins.warn(" performance ");
      return;
    }
    let m = performance.memory;
    this.showData[""] = (m.jsHeapSizeLimit / 1048576).toFixed(2) + "MB";
    this.showData[""] = (m.totalJSHeapSize / 1048576).toFixed(2) + "MB";
    this.showData[""] = (m.usedJSHeapSize / 1048576).toFixed(2) + "MB";

    let t = performance.timing;
    this.showData[""] = t.responseStart - t.navigationStart + "ms";
    this.showData["DNS"] = t.domainLookupEnd - t.domainLookupStart + "ms";
    this.showData["HTTP"] = t.responseEnd - t.requestStart + "ms";
    this.showData["TCP"] = t.connectEnd - t.connectStart + "ms";
    this.showData["load"] = t.loadEventEnd - t.loadEventStart + "ms";
    this.showData["onload"] = t.loadEventEnd - t.navigationStart + "ms";

    let dataStr: string = "";
    for (let key in this.showData) {
      dataStr += key + ": " + this.showData[key] + "\n";
    }
    // LogSystem.ins.info("Performance: \n" + dataStr);
  }

  private static getInfo(): string {
    let dataStr: string = "";
    for (let key in this.showData) {
      dataStr += key + ": " + this.showData[key] + "</br>";
    }
    return dataStr;
  }
}
