/**
 * 
 * @version 1.0
 */
export class LocalStorageUtils {
  /**
   * 
   * @param {string} key - item
   */
  public static removeItem(key: string) {
    localStorage.removeItem(key);
  }

  /**
   * Object
   * @param {string} key - 
   * @param {object} value - 
   */
  public static setObject(key: string, value: any) {
    localStorage.setItem(key, JSON.stringify(value));
  }

  /**
   * Object
   * @param {string} key - 
   * @returns {object} - 
   */
  public static getObject(key: string): any {
    let obj = localStorage.getItem(key);
    if (obj == "") {
      return null;
    }
    return JSON.parse(obj);
  }

  /**
   * Bool
   * @param {string} key - 
   * @param {boolean} value - 
   */
  public static setBool(key: string, value: boolean) {
    localStorage.setItem(key, value.toString());
  }

  /**
   * Bool
   * @param {string} key - 
   * @returns {boolean} - 
   */
  public static getBool(key: string): boolean {
    return localStorage.getItem(key) == "true" ? true : false;
  }

  /**
   * Int
   * @param {string} key - 
   * @param {number} value - 
   */
  public static setInt(key: string, value: number) {
    localStorage.setItem(key, value.toString());
  }

  /**
   * Int
   * @param {string} key - 
   * @returns {number} - 
   */
  public static getInt(key: string): number {
    return Number(localStorage.getItem(key));
  }

  /**
   * String
   * @param {string} key - 
   * @param {string} value - 
   */
  public static setString(key: string, value: string) {
    localStorage.setItem(key, value);
  }

  /**
   * String
   * @param {string} key - 
   * @returns {string} - 
   */
  public static getString(key: string): string {
    return localStorage.getItem(key);
  }
}
