/**
 * 
 * @version 1.0
 */
export class StringUtils {
  public static format(sourceStr: string, ...args): string {
    if (args.length === 0) {
      return sourceStr;
    }
    let str = sourceStr;
    for (let i = 0; i < args.length; i++) {
      str = str.replace(new RegExp("\\{" + i + "\\}", "g"), args[i]);
    }
    return str;
  }
  /**
   * 
   * @param fromIndex 0
   * @param len 
   * @param sourceStr 
   * @param replaceCode 
   * @returns string
   */
  public static replaceStr(
    fromIndex: number,
    len: number,
    sourceStr: string,
    replaceCode: string = "*"
  ): string {
    var tempStr = "";
    var tempLen = sourceStr.length;
    for (var index = 0; index < tempLen; ++index) {
      if (index >= fromIndex && index < fromIndex + len) {
        tempStr += replaceCode;
      } else {
        tempStr += sourceStr.charAt(index);
      }
    }
    return tempStr;
  }

  /**
   * 
   * @param str 
   * @returns number
   */
  public static strLength(str: string): number {
    if (str === void 0) {
      return 0;
    }
    var len = 0;
    for (var i = 0; i < str.length; i++) {
      if ((str.charCodeAt(i) & 0xff00) != 0) {
        len++;
      }
      len++;
    }
    return len;
  }

  /**
   * 
   */
  public static isEmpty(obj: any): boolean {
    if (typeof obj == "undefined" || obj == null || obj == "") {
      return true;
    } else {
      return false;
    }
  }

  /**
   * 
   * @param {string} text 
   * @param {number} max 
   * @param {string} [moreText="..."] 
   */
  public static moreText(text: string, max: number, moreText: string = "..."): string {
    if (text.length > max) {
      return text.substring(0, max - 2) + "...";
    } else {
      return text;
    }
  }
}
